.box {
  display: flex;
  flex-flow: column;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 5px auto;
  width: 500px;
  padding: 5px;
}
.box_name {
  display: flex;
  flex-flow: column;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 5px auto;
  width: 400px;
}

.button {
  background: rgba(138, 105, 105, 0.2);
  /* box-shadow: 10px 5px 10px 5px rgba(20, 1, 1, 0.89); */

  /* color: rgb(222, 228, 203); */
  color: rgb(249, 224, 248);
  padding: 0.1em;
  text-decoration: none;

  cursor: pointer;
  font-size: 10px;
  font-weight: 700;
  line-height: 2.81;
  margin: 10px 0 2em;
  max-width: 160px;
  text-decoration: none;
  text-transform: uppercase;
  width: 100%;

  border: 1px solid;
  border-radius: 5px;
}

.button:hover {
  border: px solid;
  border-radius: 5px;
  box-shadow: inset 0 0 20px rgba(223, 192, 221, 0.5),
    0 0 20px rgba(128, 102, 102, 0.2);
  outline-color: rgba(56, 53, 53, 0);
}
.input_name {
  margin-left: 20px;
  margin-bottom: 10px;
  border-radius: 5px;
  padding-left: 10px;
  font-size: 15px;
  height: 30px;
  width: 200px;
  background-color: rgb(207, 216, 240);
}
.input_number {
  margin-left: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
  padding-left: 10px;
  font-size: 15px;
  height: 30px;
  width: 200px;
  background-color: rgb(207, 216, 240);
}
